.update-range-time {
  margin-bottom: .25rem;
  text-align: center;
}
.update-range-time i {
  float: right;
  margin-right: .49rem;
}
.number-total {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 6.4rem;
  border-top: 0.01rem solid #5AB0F3;
  border-bottom: 0.01rem solid #5AB0F3;
  padding: 0.25rem 0;
  margin: 0 auto .1rem;
}
.number-total dl {
  flex: 1;
  text-align: center;
  border-right: .01rem solid #ffffff;
}
.number-total dl:last-child {
  border-right-color: transparent;
}
.number-total dl dt {
  font-size: .44rem;
  color: #F3Af1B;
}
.trans {
  padding-left: .18rem;
  padding-right: .1rem;
}
.trans .title {
  text-align: justify;
}
.new-msg {
  padding-right: .1rem;
}
